import "./import/modules";
import $ from "jquery";


$(document).ready(function () {

    $('video,.aboutSectionVideo-play').on('click', function () {
        let video = $(".aboutSectionVideo-video").get(0);


        if (video.paused) {
            video.play();
            $(".aboutSectionVideo-play").hide();
        } else {
            video.pause();
            $(".aboutSectionVideo-play").show();
        }

        return false;
    });

    $('img[data-enlargeable]').addClass('img-enlargeable').click(function () {
        var src = $(this).attr('src');
        var modal;

        function removeModal() {
            modal.remove();
            $('body').off('keyup.modal-close');
        }

        modal = $('<div>').css({
            background: 'RGBA(0,0,0,.5) url(' + src + ') no-repeat center',
            backgroundSize: 'contain',
            width: '100%', height: '100%',
            position: 'fixed',
            zIndex: '10000',
            top: '0', left: '0',
            cursor: 'zoom-out'
        }).click(function () {
            removeModal();
        }).appendTo('body');
        //handling ESC
        $('body').on('keyup.modal-close', function (e) {
            if (e.key === 'Escape') {
                removeModal();
            }
        });
    });

    $('#go-to-top').hide();

    $(window).scroll(function () {
        if ($(this).scrollTop() != 0) {
            $('#go-to-top').fadeIn();
        } else {
            $('#go-to-top').fadeOut();
        }
    });

    $('#go-to-top').each(function () {
        $(this).click(function () {
            $('html,body').animate({scrollTop: 0}, 'slow');
            return false;
        });
    });


    function setNavigation() {
        let current_location = location.pathname.split('/')[1];
        if (current_location === "") return;
        let menu_items = document.querySelectorAll(".footer-list li a");
        for (let i = 0, len = menu_items.length; i < len; i++) {
            if (menu_items[i].getAttribute("href").indexOf(current_location) !== -1) {
                menu_items[i].className = "active footer-list__items text-decoration-none";
            }
        }
    }

    setNavigation();

    function setFooterNavigation() {
        let current_location = location.pathname.split('/')[1];
        if (current_location === "") return;
        let menu_items = document.querySelectorAll(".header-nav__item a");
        for (let i = 0, len = menu_items.length; i < len; i++) {
            if (menu_items[i].getAttribute("href").indexOf(current_location) !== -1) {
                menu_items[i].className = "active text-decoration-none";
            }
        }
    }

    setFooterNavigation();


    function startScrolling(scroller_obj, velocity, start_from) {
        //bind animation  inside the scroller element
        scroller_obj.bind('marquee', function (event, c) {
            //text to scroll
            var ob = $(this);
            //scroller width
            var sw = parseInt(ob.closest('.text_wrapper').width());
            //text width
            var tw = parseInt(ob.width());
            //text left position relative to the offset parent
            var tl = parseInt(ob.position().left);
            //velocity converted to calculate duration
            var v = velocity > 0 && velocity < 100 ? (100 - velocity) * 8000 : 11000;
            //same velocity for different text's length in relation with duration
            var dr = (v * tw / sw) + v;
            //is it scrolling from right or left?
            switch (start_from) {
                case 'right':
                    //is it the first time?
                    if (typeof c == 'undefined') {
                        //if yes, start from the absolute right
                        ob.css({
                            left: sw
                        });
                        sw = -tw;
                    } else {
                        //else calculate destination position
                        sw = tl - (tw + sw);
                    }
                    ;
                    break;
                default:
                    if (typeof c == 'undefined') {
                        //start from the absolute left
                        ob.css({
                            left: -tw
                        });
                    } else {
                        //else calculate destination position
                        sw += tl + tw;
                    }
                    ;
            }
            //attach animation to scroller element and start it by a trigger
            ob.animate({
                left: sw
            }, {
                duration: dr,
                easing: 'linear',
                complete: function () {
                    ob.trigger('marquee');
                },
                step: function () {
                    //check if scroller limits are reached
                    if (start_from == 'right') {
                        if (parseInt(ob.position().left) < -parseInt(ob.width())) {
                            //we need to stop and restart animation
                            ob.stop();
                            ob.trigger('marquee');
                        }
                        ;
                    } else {
                        if (parseInt(ob.position().left) > parseInt(ob.parent().width())) {
                            ob.stop();
                            ob.trigger('marquee');
                        }
                        ;
                    }
                    ;
                }
            });
        }).trigger('marquee');
        //pause scrolling animation on mouse over
        /*        scroller_obj.mouseover(function () {
                    $(this).stop();
                });*/
        //resume scrolling animation on mouse out
        scroller_obj.mouseout(function () {
            $(this).trigger('marquee', ['resume']);
        });
    };


    $(function () {
        $('.text_wrapper').each(function (i, obj) {
            if ($(this).find('.text_overflow').width() > $(this).width()) {
                //settings to pass to function
                var scroller = $(this).find('.text_overflow'); // element(s) to scroll
                var scrolling_velocity = 95; // 1-99
                var scrolling_from = 'right'; // 'right' or 'left'
                //call the function and start to scroll..
                startScrolling(scroller, scrolling_velocity, scrolling_from);
            }
        });
    });

    $(".owl-carousel-1").owlCarousel({
        autoplay: true,
        center: true,
        nav: true,
        navText: ["<img src='img/arrow-left.png'>", "<img src='img/arrow-right.png'>"],
        autoplayTimeout: 4000,
        autoplayHoverPause: true,
        loop: true,
        margin: 15,

        responsiveClass: true,
        responsive: {
            0: {
                items: 1,
                nav: true,
                dots: false,
                margin: 25
            },
            600: {
                items: 1,
                nav: true,
                dots: false,
                margin: 25
            },
            768: {
                items: 1.8,
                nav: true,
                dots: false,
                margin: 120
            },
            1024: {
                items: 2,
                nav: true,
                dots: false,
                margin: 120
            },

            1366: {
                items: 2.4,
                nav: true,
                dots: false,
                stagePadding: 60,
                margin: 65
            },
            1920: {
                items: 2.9,
                nav: true,
                dots: false,
                stagePadding: 30,
                margin: 50
            },
            1921: {
                items: 3,
                nav: true,
                dots: false,
                stagePadding: 60,
                margin: 65
            }
        },
    });
    $(".owl-carousel-2").owlCarousel({
        autoplay: true,
        center: true,
        nav: false,
        autoplayTimeout: 2000,
        autoplayHoverPause: true,
        loop: true,
        margin: 15,
        animateOut: 'fadeOut',
        animateIn: 'fadeIn',

        responsiveClass: true,
        responsive: {
            0: {
                items: 1,
                nav: false,
                dots: false,
                margin: 25
            },
            600: {
                items: 1,
                nav: false,
                dots: false,
                margin: 25
            },
            768: {
                items: 1.8,
                nav: false,
                dots: false,
                margin: 120
            },
            1024: {
                items: 2,
                nav: false,
                dots: false,
                margin: 120
            },

            1366: {
                items: 2.6,
                nav: false,
                dots: false,
                margin: 40
            },
            1920: {
                items: 2.4,
                nav: false,
                dots: false,
                stagePadding: 60,
                margin: 65
            },
            1921: {
                items: 3,
                nav: false,
                dots: false,
                stagePadding: 60,
                margin: 65
            }
        },
    });
});


$('.header-bars').on('click', function () {
    $(this).hide(500);
    $('.header-mobile').show(100);
    $(".header-mobile").animate({
        left: '0px',
    }, "slow");
});


$('.header-mobile__bar').on('click', function () {
    $(".header-mobile").animate({
        left: '-2000px',
        top: "0px",
    }, "slow");
    $('.header-mobile').hide(500);
    $(".header-bars").show(500);
});

$(".swiper-container").mouseenter(function () {
    swiper.autoplay.stop();
});

$(".swiper-container").mouseleave(function () {
    swiper.autoplay.start();
});




